.featured {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px;
    width: 85%;
    margin: 15px auto;
    color: black;
}
.featured h1 {
    font-size: 1.8rem;
    padding-top: 15px;
    margin-bottom: 8px;
    color: #333;
    text-align: center;
}
.featured hr {
    width: 120px;
    height: 2px;
    background: #000000;
    margin-bottom: 20px;
}
.featured-item {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 15px;
}
.featured-item > div {
    display: flex;
    flex-direction: column;
    width: 220px;
    height: 300px;
    border: 1px solid #ddd;
    padding: 8px;
    box-sizing: border-box;
}
.featured-item img {
    width: 100%;
    height: 200px;
    object-fit: cover;
    display: block;
    margin: 0 auto;
}

@media (max-width: 768px) {
    .featured {
        width: 90%;
        padding: 10px;
        margin: 10px auto;
    }
    .featured hr {
        width: 100px;
        margin-bottom: 15px;
    }
    .featured-item > div {
        width: 85%;
        max-width: 250px;
        height: auto;
        margin: 8px auto;
    }
    .featured-item img {
        height: auto;
        max-height: 200px;
    }
}
@media (max-width: 480px) {
    .featured {
        padding: 8px;
        margin: 8px auto;
    }
    .featured h1 {
        font-size: 1.4rem;
        padding-top: 8px;
        margin-bottom: 3px;
    }
    .featured hr {
        width: 80px;
        margin-bottom: 10px;
    }
    .featured-item > div {
        width: 90%;
    }
}

@media (max-width: 480px) {
    .filter-dropdown {
        margin-top: 10px;
        margin-bottom: 10px;
        padding: 0 8px;
    }
    .filter-dropdown select {
        padding: 8px 15px;
        font-size: 0.9rem;
        max-width: 90%;
        width: 90%;
        margin: 0 auto;
        display: block;
        border-radius: 6px;
    }
    .featured h1 {
        font-size: 1.3rem;
        padding-top: 70px; 
        margin-bottom: 5px; 
    }
}